@import "./../../constants/colors.scss";

.wrap_shadow {
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

.confirm_wrap_blur {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.data_container {
  width: 55vw;
  z-index: 1;
  padding: 24px;
  border-radius: 16px;
  background-color: $base-color;
  position: relative;
  display: grid;
  gap: 16px 13px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 48px 100px 100px 1fr 1fr 48px;
  grid-template-areas:
    "text_area text_area"
    "images_container name_input"
    "images_container price_input"
    "input_images_container description_input"
    "type_dishes description_input"
    "add_button add_button";
}
.lable {
  color: $white-color;
}
.input {
  margin-top: 8px;
  font-family: "Comfortaaa-600";
}
img {
  text-align: center;
}

.add_new_item_text {
  grid-area: text_area;
  color: $white-color;
  font-family: "Comfortaaa-700";
  font-size: 24px;
}
.images_container {
  grid-area: images_container;
  display: flex;
  justify-content: center;
  align-items: center;
}
.current_image {
  display: block;
  aspect-ratio: 1 / 1;
  width: 70%;
  border-radius: 8px;
  background-color: $third-color;
}
.input_images_container {
  grid-area: input_images_container;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  overflow: hidden;
}

.input_image {
  margin-top: 8px;
  display: none;
}
.name_input {
  grid-area: name_input;
}

.price_input {
  grid-area: price_input;
}
.description_input {
  grid-area: description_input;
}
.type_dishes {
  grid-area: type_dishes;
}
.note {
  resize: none;
  height: 140px;
  width: 100%;
  padding: 12px;
  line-height: 22px;
  margin-top: 8px;
  background-color: $fourth-color;
  font-family: "Comfortaaa-500";
  color: $white-color;
  border: 1px solid $third-color;
  border-radius: 8px;
  overflow-y: initial;
}
.note:focus {
  outline: none;
  border-color: $white-color;
}

.add_button {
  cursor: pointer;
  grid-area: add_button;
  border: 1px solid $primary-color;
  border-radius: 8px;
  background-color: inherit;
  color: $primary-color;
  font-family: "Comfortaaa-700";
  font-size: 16px;
}
.add_button:hover {
  color: $white-color;
  background-color: $primary-color;
}

.type_text {
  color: white;
  margin-bottom: 8px;
}
.exit_button {
  cursor: pointer;
  background-color: inherit;
  border: none;
  border-radius: 8px;
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 0;
}

.exit_icon {
  color: $white-color;
}

label.label input[type="file"] {
  position: absolute;
}
.lable_upload_image {
  cursor: pointer;
  width: 70%;
  height: 48px;
  padding: 12px;
  background-color: $fourth-color;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid $third-color;
  border-radius: 8px;
  color: $white-color;
  white-space: nowrap;
  overflow: auto;
}
.lable_upload_image::-webkit-scrollbar {
  display: none;
}
.lable_upload_image:hover {
  background-color: $white-color;
  color: $third-color;
}

@media screen and (max-width: 1150px) {
  .data_container {
    width: 60vw;
  }
}

@media screen and (max-width: 991px) {
  .data_container {
    width: 70vw;
  }
}
@media screen and (max-width: 769px) {
  .data_container {
    width: 80vw;
  }
}
