@import "./../../constants/colors.scss";

.container {
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: $secondary-color;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logIn_container {
  padding: 24px;
  width: 60vw;
  background-color: $base-color;
  border-radius: 8px;
  display: grid;
  gap: 24px 16px;
  grid-template-columns: 1fr 1fr 1px 1fr 1fr;
  grid-template-rows: max-content 1px 1fr 1fr 48px;
  grid-template-areas:
    "logIn_text logIn_text . exit exit"
    "horz_line horz_line horz_line horz_line horz_line"
    "email_input email_input vert_line  logIn_other_way logIn_other_way "
    "password_input  password_input  vert_line logIn_other_way logIn_other_way "
    "logIn_buttons logIn_buttons vert_line signUp_button signUp_button ";
}
.logIn_text {
  grid-area: logIn_text;
  font-family: "Comfortaaa-700";
  font-size: 28px;
  color: $white-color;
  text-align: start;
}
.exit {
  grid-area: exit;
  text-decoration: none;
  color: $white-color;
  text-align: end;
}
.horz_line {
  grid-area: horz_line;
  height: 100%;
  background-color: $third-color;
}
.vert_line {
  grid-area: vert_line;
  height: 100%;
  background-color: $third-color;
}

.email_lable,
.password_lable {
  margin-bottom: 8px;
}
.email_container,
.password_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  color: $white-color;
}
.email_container {
  grid-area: email_input;
}
.password_container {
  grid-area: password_input;
}

.logIn_buttons {
  grid-area: logIn_buttons;
}
.signUp_button {
  grid-area: signUp_button;
}
.logIn_other_way {
  grid-area: logIn_other_way;
  display: flex;
  flex-direction: column;
}
.logIn_other_way_text {
  font-family: "Comfortaaa-700";
  color: $white-color;
  font-size: 18px;
  margin-bottom: 10px;
}
.google,
.facebook {
  height: 48px;
  background-color: $fourth-color;
  margin-bottom: 24px;
  text-align: center;
  color: $white-color;
  border: 1px solid $primary-color;
  font-family: "Comfortaaa-700";
  border-radius: 8px;
  font-size: 20px;
  cursor: pointer;
}
.google:hover,
.facebook:hover {
  background-color: $white-color;
  color: $fourth-color;
}

@media screen and (max-width: 991px) {
  .logIn_container {
    width: 70vw;
  }
}
@media screen and (max-width: 756px) {
  .logIn_container {
    width: 80vw;
  }
}

@media screen and (max-width: 670px) {
  .logIn_container {
    width: 90vw;
  }
}
